
import Vue from 'vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import { getSingleUserInfo, fetch, setUserInfo } from '@/api/base'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)


export default {
components: {
XTextarea,
Group,
XButton,
Box,
XHeader
},
data () {
return {
declaration: getSingleUserInfo('user_curatorDeclaration')
}
},
mounted () {

},
methods: {
getAxios: function () {











},
changeNotice: function () {
let me = this
fetch('/api/v1/agents/chessDeclaration', {declaration: me.declaration}, response => {
let data = response.data
let msg = response.data.msg || response.data.error || '未知错误'
me.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
setUserInfo('user_curatorDeclaration', me.declaration)
me.$router.go(-1)
}, error => {
console.log(error)
})
}
}
}



